/* ==========================================================================
   Product List - Slider shortcode responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	.mkd-plvdsl-holder {
		.mkd-pvd-left {
			.mkd-pli-content {
				padding: 0 0 0 23px;
			}

			.mkd-pvd-add-to-cart .add_to_cart_button {
				bottom: -12px;
			}
		}
	}
}

@include phone-landscape {
	.mkd-plvdsl-holder .mkd-pvd-left {
		display: block;

		.mkd-pli-inner {
			display: block;
			width: 100%;
		}

		.mkd-pli-content {
			display: block;
			width: 100%;
		}
	}
}

@include phone-portrait {
	.mkd-plvdsl-holder {
		.mkd-plvdsl-deals {
			padding: 0 15px;
		}

		.mkd-owl-slider .owl-nav .owl-prev {
			width: 40px;
			left: 218px;
		}


		.mkd-owl-slider .owl-nav .owl-next {
			width: 40px;
			left: 258px;
		}

		.mkd-pvd-left {
			padding: 106px 41px 46px;

			.mkd-pli-content {
				padding: 20px 0 20px 0;
			}

			.mkd-pvd-add-to-cart .add_to_cart_button {
				bottom: 14px;
			}
		}
	}
}

/* ==========================================================================
   Product List - Slider shortcode responsive styles - end
   ========================================================================== */