@include laptop-landscape-large {
    .mkd-popup-compare {
        #colorbox {
            width: 73% !important;
        }

        .mkd-pl-holder {
            .mkd-pli-popup {

                .mkd-pli-holder-popup {
                    padding: 10px 25px;
                }


            }
        }
    }
}

@include ipad-landscape {
    .mkd-popup-compare {
        #colorbox {
            width: 77% !important;
        }
        .mkd-pl-holder {
            .mkd-pli-popup {
                width: 50%;
            }

            .mkd-pli-inner {
                display: block;
            }

            .mkd-pl-outer-popup {
                .mkd-item-space {

                    &:nth-child(n+3) {
                        margin-top: 0;
                    }

                }
            }
        }
    }

}


@include ipad-portrait {

    .mkd-popup-compare {
        .mkd-pl-holder {
            .mkd-pli-popup {
                width: 100% !important;
            }

        }

        .mkd-pl-outer-popup {
            .mkd-item-space {

                &:nth-child(n+2) {
                    margin-top: 0;
                }

            }
        }
    }

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }


}

@media only screen and (max-width: $phone-landscape) {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
                margin: 0 70% 15px 0;
            }
        }
    }
}

@include phone-portrait {
    .mkd-popup-compare {
       #cboxLoadedContent {
           width: 100% !important;
       }
    }

}