/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include laptop-landscape-medium {
	.mkd-woocommerce-page {

		div.woocommerce .mkd-quantity-buttons .mkd-quantity-label {
			margin-right: 0;
		}

		.cart-collaterals {

			table th {
				padding-right: 16px;
			}

			.woocommerce-shipping-calculator .shipping-calculator-button:after {
				margin-left: 5px;
			}
		}
	}
}

@include ipad-landscape {
	.mkd-woocommerce-page.woocommerce-cart {
		.woocommerce>form,
		.cart-collaterals {
			float: none;
			margin-bottom: 80px;
			width: 100%;
		}
	}

	.mkd-woocommerce-page {

		.cart-empty + p {
			padding: 0 18%;
		}
	}
}

@include ipad-portrait {

	.mkd-woocommerce-page {

		.mkd-content .mkd-quantity-buttons .mkd-quantity-input,
		div.woocommerce .mkd-quantity-buttons .mkd-quantity-input {
			margin: 0;
		}


		div.woocommerce .mkd-quantity-buttons .mkd-quantity-label {
			margin-right: 0;
		}

		.mkd-content .mkd-quantity-buttons .mkd-quantity-label {
			margin-right: 31px;
		}

		.cart-empty + p {
			padding: 0 2%;
		}

		table.cart {
			tr.cart_item {
				.product-subtotal {
					width: 10%;
				}

				.product-subtotal,
				.product-price {
					font-size: 18px;
				}

				td.product-thumbnail {
					display: none;
				}

				.product-quantity {
					width: 26%;
				}
			}

			td.actions {

				.coupon {

					input[type="text"] {
						width: 115px;
						margin: 0;
						padding: 9px 0px;
					}
				}

				.button {
					height: 38px;
					line-height: 38px;
					padding: 0 28px;
				}
			}
		}
	}
}

@include phone-landscape {

	.mkd-woocommerce-page.woocommerce-cart {
		.woocommerce > form table.cart {
			tr.cart_item{
				td.product-thumbnail,
				td.product-price{
					display: none;
				}

				td.product-name{
					a{
						top: 2px;
						position: relative;
					}
				}

				.mkd-quantity-buttons{
					background-color: transparent;
					border: none;
					margin: 0;
					padding: 0;
					width: 80px;

					.mkd-quantity-input {
						width: 27px;
					}

					.mkd-quantity-label{
						display: none;
					}
				}
			}

			td.actions {
				.coupon {
					float: none;
					width: 100%;
				}
				button[name="apply_coupon"],
				button[name="update_cart"] {
					float: right;
				}
			}

		}

		.cart-collaterals {

			tr {
				display: inline-block;
				width: 100%;

				th {
					float: left;
				}
				td {
					float: right;
				}

				&.shipping {
					th, td {
						float: none;
						display: inline-block;
						width: 100%;
						padding: 0;
					}
				}
			}
		}


	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */