/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@include laptop-landscape-medium {
    .mkd-woo-single-page {
        .mkd-single-product-content {
            .mkd-labels-holder {
                left: 135px;
            }
        }
    }
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .mkd-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}


@include ipad-landscape {
    
    .mkd-woo-single-page {
        
        .mkd-single-product-content {

            .mkd-labels-holder {
                left: 175px;
            }
            
            .images,
            .mkd-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkd-single-product-summary {
                margin: 75px 0 0;

                .mkd-quantity-buttons {
                    margin: 0 -4px 0 0;
                }
            }

            button[type="submit"]:not(.mkd-woo-search-widget-button) {
              padding: 13px 31px 12px;
            }
        }

        .mkd-cross-sells-accessories-tab {
            .mkd-total-price {
                .mkd-total-price-items {
                    text-align: right;
                }
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }

            #reviews {
                ol.commentlist {
                    .comment-text {
                        padding: 0 10% 0 108px;
                    }
                }

                .comment-respond {
                    .comment-form-comment {
                        width: 100%;
                    }
                }
            }
        }
    }

  .woocommerce-page {
      .mkd-content {
          input[type=submit] {
            padding: 13px 31px;
          }
          a.button {
            padding: 0;
          }
      }
  }
}

@include ipad-portrait {
  .mkd-woo-single-page {
      .mkd-single-product-content {
          .mkd-labels-holder {
              left: 10px;
          }

          .mkd-single-product-summary {
              margin: 30px 0 0;
          }
      }

      .mkd-cross-sells {
          >.products {
              >.product {
                  width: 100%;
              }
          }
      }

  }
}

@include phone-landscape {
    
    .mkd-woo-single-page {
	
	    &.mkd-woo-single-thumb-on-left-side {
		
		    .mkd-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .mkd-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }


        .woocommerce-tabs {

            ul.tabs {

                > li {

                    a {
                        padding: 13px 13px 0 0;
                    }
                }
            }
        }
    }
}

@include phone-portrait {
    .mkd-woo-single-page {
        .mkd-single-product-summary {
            .mkd-quantity-buttons {
                margin: 0 0px 15px 0;
            }
        }


    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */