@include laptop-landscape-large {
    .mkd-pvd-left {
        .mkd-pli-content {
            .mkd-pvd-compare {
                right: 5%;
            }
        }
    }

    .mkd-pvd-middle {
        padding: 21px 20px 30px;

        .mkd-pvd-compare {
            right: 15px;
        }
    }


}

@include laptop-landscape-medium {
    .mkd-pvd-left {
        .mkd-pli-content {
            .mkd-pvd-add-to-cart {
                .add_to_cart_button,
                .product_type_external,
                .added_to_cart {
                    bottom: 6px;
                }

            }

            .mkd-pvd-compare {
                right: 6%;
            }
        }
    }

    .mkd-pvd-middle {
        padding: 21px 20px 30px;
        .mkd-pvd-add-to-cart {
            .add_to_cart_button, .product_type_external {
                bottom: -27px;
            }
        }

        .mkd-pvd-compare {
            right: 12px;
        }
    }

}

@include phone-portrait {
    .mkd-pvd-left {

        .mkd-pli-inner {
            display: block;
            width: 100%;
            text-align: center;

            .mkd-pli-image {
                padding: 23px;
            }
        }

        .mkd-pli-content {
            display: block;
            width: 100%;
            padding: 0 30px 30px;

            .mkd-pvd-compare {
                top: 2%;
                right: 21%;
            }

            .mkd-pvd-add-to-cart {
                .add_to_cart_button,
                .added_to_cart,
                .product_type_external{
                    right: 17%;
                }

            }
        }
    }
}

