/* ==========================================================================
   Product List Advanced shortcode responsive styles - begin
   ========================================================================== */
@include laptop-landscape-medium {
    .mkd-pl-holder.mkd-pl-holder-advanced .mkd-pli {
        &.mkd-pla-position-middle {
            .mkd-pla-image {
                margin: 0 0 10px;
            }
        }

        .mkd-pla-add-to-cart {
            bottom: 16px;
        }
    }
}


@include ipad-landscape {
    .mkd-pl-holder.mkd-masonry-type.mkd-pl-holder-advanced.mkd-three-columns .mkd-pli {
        width: 100%;
        height: auto !important;
    }
}

/* ==========================================================================
   Product List Advanced shortcode responsive styles - end
   ========================================================================== */