@include laptop-landscape-medium {
  .mkd-woocommerce-page .mkd-content {
      table.group_table {
          td {
              &.label {
                  max-width: 150px;
              }

              &.price {
                  width: 190px;

                  del .amount {
                      left: 31%;
                      font-size: 14px;
                  }
              }
          }
      }
  }
}

@include ipad-landscape {
    .mkd-woocommerce-page .mkd-content {
        table.group_table {
            td {
                &.label {
                    max-width: 450px;
                }

                &.price {
                    width: 200px;

                    del .amount {
                        font-size: 16px;
                    }
                }
            }
        }
    }
}

@include ipad-portrait {
    .mkd-woocommerce-page .mkd-content {
        table.group_table {
            td {
                &.label {
                    max-width: 300px;
                }

                &.price {
                    width: 190px;

                    del .amount {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}


@include phone-portrait {
    .mkd-woocommerce-page .mkd-content {
        table.group_table {
            td {
                &.price {
                    width: 77px;
                    p.stock.in-stock {
                        display: none;
                    }

                    del .amount {
                        top: 75%;
                        left: 0;
                        font-size: 17px;
                    }
                }

                &.label {
                    max-width: 110px;
                }
            }
        }
    }
}