/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */
@include ipad-landscape {
    .woocommerce-page .mkd-content,
    div.woocommerce {
        input[type="email"],
        #respond input[type=text] {
            width: 100%;
        }
    }
}



@include ipad-portrait {

    .woocommerce-page .mkd-content,
    div.woocommerce {

        .mkd-quantity-buttons {

            .mkd-quantity-input,
            .mkd-quantity-minus,
            .mkd-quantity-plus {
                font-size: 13px;
            }

            .mkd-quantity-input {
                height: 38px;
                width: 27px;
                margin: 0;
                line-height: 36px;

            }

            .mkd-quantity-minus,
            .mkd-quantity-plus {
                width: 19px;
                height: 19px;
                line-height: 17px;
            }
        }
    }
}

@include phone-portrait {
    .woocommerce-page .mkd-content,
    div.woocommerce {
        .mkd-quantity-buttons {
            .mkd-quantity-input {
                margin: 0 0 3px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */