/* ==========================================================================
   Product List shortcode responsive styles - begin
   ========================================================================== */
@include laptop-landscape-large {

    .mkd-pl-holder {
	    
	    &.mkd-standard-type:not(.mkd-filter-position-left) {
		    $standard_list_columns: ('six');
		
		    @for $i from 0 to length($standard_list_columns) {
			    &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
				    $column_width: 100%/5;
				
				    .mkd-pli {
					    width: $column_width;
					
					    @media only screen and (min-width: $laptop-landscape-plus-pixel) {
						    &:nth-child(6n+1) {
							    clear: none;
						    }
						
						    &:nth-child(5n+1) {
							    clear: both;
						    }
					    }
				    }
			    }
		    }
	    }
	    
	    &.mkd-masonry-type {
		    $masonry_list_columns: ('five', 'six');
		
		    @for $i from 0 to length($masonry_list_columns) {
			    &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
				    $column_width: 100%/4;
				
				    .mkd-pli,
				    .mkd-pl-sizer {
					    width: $column_width;
				    }
				
				    .mkd-pli.mkd-woo-image-large-width {
					    width: $column_width*2;
				    }
			    }
		    }
	    }

		&.mkd-filter-position-left .mkd-pl-filters .mkd-pl-filters-inner {
			padding: 33px 30px 0;
		}
    }
}


@include laptop-landscape {

    .mkd-pl-holder {
	    
	    &.mkd-standard-type:not(.mkd-filter-position-left) {
		    $standard_list_columns: ('five', 'six');
		
		    @for $i from 0 to length($standard_list_columns) {
			    &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
				    $column_width: 100%/4;
				
				    .mkd-pli {
					    width: $column_width;
					    $standard_list_columns_label: #{nth($standard_list_columns,$i+1)};
					
					    @media only screen and (min-width: $ipad-landscape-plus-pixel) {
						    @if $standard_list_columns_label == "five" {
							    &:nth-child(5n+1) {
								    clear: none;
							    }
						    } @else if $standard_list_columns_label == "six" {
							    &:nth-child(6n+1) {
								    clear: none;
							    }
						    }  @else if $standard_list_columns_label == "three" {
								&:nth-child(3n+1) {
									clear: none;
								}
							}
						
						    &:nth-child(4n+1) {
							    clear: both;
						    }
					    }
				    }
			    }
		    }
	    }
	    
	    &.mkd-masonry-type {
		    $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		    @for $i from 0 to length($masonry_list_columns) {
			    &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
				    $column_width: 100%/($i+2);
				
				    @if ($i+2) == 5 {
					    $column_width: 100%/4;
				    } @else if ($i+2) == 6 {
					    $column_width: 100%/4;
				    }
				
				    .mkd-pli,
				    .mkd-pl-sizer {
					    width: $column_width;
				    }
				
				    .mkd-pli.mkd-woo-image-large-width {
					    width: $column_width*2;
				    }
			    }
		    }
	    }
    }
}

@include ipad-landscape {

    .mkd-pl-holder {

		&.mkd-filter-position-left {

			.mkd-pl-outer-holder {
				width: 66.6666%;
			}

			.mkd-pl-filters {
				width: 33.3333%;
			}
		}

		&.mkd-standard-type.mkd-filter-position-left {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkd-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 50%;

					.mkd-pli {
						width: $column_width;

						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							&:nth-child(2n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		&.mkd-standard-type.mkd-filter-position-top {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkd-#{nth($standard_list_columns,$i+1)}-columns {

					.mkd-pli {
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							&:nth-child(#{$i+2}n) {

								&:after {
									display: none;
								}
							}
						}
					}
				}
			}
		}
	    
	    &.mkd-standard-type:not(.mkd-filter-position-left) {
		    $standard_list_columns_clear: ('two', 'three');
		    $standard_list_columns: ('four', 'five', 'six');
		
		    @for $i from 0 to length($standard_list_columns) {
			    &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
				    $column_width: 100%/3;
				
				    .mkd-pli {
					    width: $column_width;
					
					    @media only screen and (min-width: $ipad-portrait-plus-pixel) {
						    &:nth-child(3n+1) {
							    clear: both;
						    }
					    }



				    }
			    }
		    }
		
		    @for $i from 0 to length($standard_list_columns_clear) {
			    &.mkd-#{nth($standard_list_columns_clear,$i+1)}-columns {
				    .mkd-pli {
					
					    @media only screen and (min-width: $ipad-portrait-plus-pixel) {
						    &:nth-child(#{$i+2}n+1) {
							    clear: both;
						    }
					    }
				    }
			    }
		    }
	    }
	    
	    &.mkd-masonry-type {
		    $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		    @for $i from 0 to length($masonry_list_columns) {
			    &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
				    $column_width: 50%;
				
				    @if ($i+2) != 2 {
					    $column_width: 100%/3;
				    }
				
				    .mkd-pli,
				    .mkd-pl-sizer {
					    width: $column_width;
				    }
				
				    .mkd-pli.mkd-woo-image-large-width {
					    width: $column_width*2;
				    }

					.mkd-pli {

						@media only screen and (min-width: $ipad-portrait-plus-pixel) {

							&:nth-child(#{$i+2}n+2) {

								&:after {
									display: none;
								}
							}
						}
					}
			    }
		    }
	    }

		&.mkd-pl-holder-advanced.mkd-standard-type:not(.mkd-filter-position-left) {
			$advanced_list_columns_clear: ('two', 'three');
			$advanced_list_columns: ('four', 'five', 'six');

			@for $i from 0 to length($advanced_list_columns_clear) {
				&.mkd-#{nth($advanced_list_columns_clear,$i+1)}-columns {
					$column_width: 100%/2;

					.mkd-pli {
						width: $column_width;

						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							&:nth-child(3n+1) {
								clear: none;
							}
						}

					}
				}
			}
		}
    }
}

@include ipad-portrait {

    .mkd-pl-holder {

		&.mkd-masonry-type {
			&.mkd-filter-position-left {
				.mkd-pl-filters {
					width: 100%;
					margin-right: 0;
				}

				.mkd-pl-outer-holder {
					padding-left: 0;
					width: 100%;
					margin: 40px 0 0;
				}
			}
		}

		&.mkd-filter-position-left {

			.mkd-pl-outer-holder {
				padding-left: 0;
				margin: 40px 0 0;
				width: auto;
			}

			.mkd-pl-filters {
				width: 100%;
			}

			&.mkd-pli-list {
				.mkd-pl-outer-holder {
					margin: 40px 0 0;
				}
			}
		}

		&.mkd-standard-type.mkd-filter-position-left {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkd-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 50%;

					.mkd-pli {
						width: $column_width;

						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							&:nth-child(2n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		&.mkd-standard-type.mkd-filter-position-top {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkd-#{nth($standard_list_columns,$i+1)}-columns {

					.mkd-pli {
						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							&:nth-child(2n) {

								&:after {
									display: none;
								}
							}
						}
					}
				}
			}
		}
	    
	    &.mkd-standard-type:not(.mkd-filter-position-left) {
		    $standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		    @for $i from 0 to length($standard_list_columns) {
			    &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
				    $column_width: 50%;
				
				    .mkd-pli {
					    width: $column_width;
					
					    @media only screen and (min-width: $phone-landscape-plus-pixel) {
						    &:nth-child(2n+1) {
							    clear: both;
						    }
					    }
				    }
			    }
		    }
	    }
	    
	    &.mkd-masonry-type {
		    $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		    @for $i from 0 to length($masonry_list_columns) {
			    &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
				    $column_width: 50%;
				
				    .mkd-pli,
				    .mkd-pl-sizer {
					    width: $column_width;
				    }
				
				    .mkd-pli.mkd-woo-image-large-width {
					    width: $column_width*2;
				    }

					.mkd-pli {

						@media only screen and (min-width: $phone-landscape-plus-pixel) {

							&:nth-child(2n) {

								&:after {
									display: none;
								}
							}
						}
					}
			    }
		    }
	    }
		&.mkd-pl-holder-advanced.mkd-standard-type:not(.mkd-filter-position-left) {
			$advanced_list_columns_clear: ('two', 'three');
			$advanced_list_columns: ('four', 'five', 'six');

			@for $i from 0 to length($advanced_list_columns_clear) {
				&.mkd-#{nth($advanced_list_columns_clear,$i+1)}-columns {
					$column_width: 100%;

					.mkd-pli {
						width: $column_width;

					}
				}
			}
		}
    }
}

@include phone-landscape {

    .mkd-pl-holder {

    	.mkd-pl-categories ul li {
    		width:100%;

    		&.mkd-filters-line {
    			display:none;
    		}
    	}

		&.mkd-filter-position-left {

			.mkd-pl-outer-holder {
				width: auto;
				margin: 40px 0 0;

				.mkd-pli {

					.mkd-pli-inner {
						width: 100%;
					}
				}
			}

			.mkd-pl-filters {
				width: 100%;
			}

			&.mkd-pli-list {

				.mkd-pl-outer .mkd-pli {
					padding: 40px 0;

					.mkd-pli-holder {
						display: block;
					}
				}

				.mkd-pli-inner,
				.mkd-pli-text-wrapper,
				.mkd-pli-cart-wrapper {
					display: block;
					width: 100%;
					text-align: center;
				}
			}
		}

		&.mkd-filter-position-top {

			.mkd-pl-outer-holder {
				width: auto;
				margin: 40px 0 0;

				.mkd-pli {
					padding: 0;

					.mkd-pli-inner {
						width: 100%;
					}
				}
			}

			&.mkd-pli-list {

				.mkd-pl-outer .mkd-pli {
					padding: 40px 0;

					.mkd-pli-holder {
						display: block;
					}
				}

				.mkd-pli-inner,
				.mkd-pli-text-wrapper,
				.mkd-pli-cart-wrapper {
					padding: 0;
					display: block;
					width: 100%;
					text-align: center;
				}
			}
		}

		&.mkd-standard-type.mkd-filter-position-left {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkd-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 100%;

					.mkd-pli {
						width: $column_width;

						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							&:nth-child(2n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
        
        &.mkd-standard-type:not(.mkd-filter-position-left) {
            $standard_list_columns: ('two', 'three', 'four', 'five', 'six');
    
            @for $i from 0 to length($standard_list_columns) {
                &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
                    $column_width: 100%;
            
                    .mkd-pli {
                        width: $column_width;
                    }
                }
            }
        }

		&.mkd-standard-type.mkd-filter-position-top {

			.mkd-pl-filters .mkd-pl-order-filter-holder .mkd-pl-ordering-outer {
				padding: 10px 10px;
				width: 280px;
			}

			.mkd-pli {
				.mkd-pli-inner {
					width: 100%;
				}

				&:after {
					display: none;
				}
			}
		}
        
        &.mkd-masonry-type {
            $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
    
            @for $i from 0 to length($masonry_list_columns) {
                &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
                    .mkd-pli,
                    .mkd-pl-sizer,
                    .mkd-pli.mkd-woo-image-large-width {
                        width: 100%;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Product List shortcode responsive styles - end
   ========================================================================== */